/* custom checkbox styles */
.jcf-checkbox {
  vertical-align: middle;
  display: inline-block;
  position: relative;
  overflow: hidden;
  cursor: default;
  border-radius: 4px;
  background-color: #fff;
  border: solid 1px #cdcdcd;
  margin: 0 18px -8px -44px;
  height: 25px;
  width: 25px;
}
.checkbox:hover .jcf-checkbox {
  background: #fff;
}
.checkbox:hover .jcf-checkbox span {
  border-color: #5c5c5c;
  display: block;
}
.checkbox:hover .jcf-checkbox.jcf-checked {
  background: #fff;
}
.checkbox:hover .jcf-checkbox.jcf-checked span {
  border-color: #5c5c5c;
  display: block;
}
.jcf-checkbox span {
  position: absolute;
  display: none;
  height: 9px;
  width: 15px;
  top: 50%;
  left: 50%;
  margin: -7px 0 0 -6px;
  border: 3px solid #fff;
  border-width: 0 0 4px 4px;
  -ms-transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  -ms-filter: "progid:DXImageTransform.Microsoft.Matrix(M11=0.7071067811865483, M12=0.7071067811865467, M21=-0.7071067811865467, M22=0.7071067811865483, SizingMethod='auto expand')";
}
:root .jcf-checkbox span {
  margin: -6px 0 0 -7px;
}
.jcf-checkbox input[type="checkbox"] {
  position: absolute;
  width: 100%;
  height: 100%;
  border: 0;
  margin: 0;
  left: 0;
  top: 0;
}
.jcf-checkbox.jcf-checked {
  background: #fff;
}
.jcf-checkbox.jcf-checked span {
  display: block;
  border-color: #5c5c5c;
}
.jcf-radio {
  vertical-align: middle;
  display: inline-block;
  position: relative;
  overflow: hidden;
  cursor: default;
  background: #fff;
  border: 1px solid #cdcdcd;
  border-radius: 100%;
  margin: -2px 3px 0 -36px;
  height: 25px;
  width: 25px;
  cursor: pointer;
}
.jcf-radio span {
  display: none;
  position: absolute;
  top: 6px;
  left: 6px;
  right: 6px;
  bottom: 6px;
  background: #5c5c5c;
  border-radius: 100%;
  cursor: pointer;
}
.jcf-radio input[type="radio"] {
  position: absolute;
  height: 100%;
  width: 100%;
  border: 0;
  margin: 0;
  left: 0;
  top: 0;
}
.jcf-radio.jcf-checked span {
  display: block;
}
